body {
  background: linear-gradient(135deg, #a1ffce 0%, #faffd1 100%);
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Poppins, sans-serif;
  display: flex;
}

ul, li {
  list-style: none;
}

h1 {
  text-align: center;
  color: #ff4500;
  margin-bottom: 20px;
  font-size: 3rem;
}

#createPostForm {
  background: linear-gradient(135deg, #00c3ff 0%, #ffff1c 100%);
  border-radius: 16px;
  flex-direction: column;
  gap: 18px;
  width: 100%;
  max-width: 500px;
  margin-bottom: 40px;
  padding: 25px 30px;
  display: flex;
  box-shadow: 0 8px 20px #0003;
}

#nameInput, #titleInput, #contentInput {
  box-sizing: border-box;
  border: 2px solid #20b2aa;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 1rem;
}

#createPostBtn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #38ef7d 0%, #11998e 100%);
  border: none;
  border-radius: 10px;
  padding: 14px 18px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: transform .3s, background .3s;
}

#createPostBtn:hover {
  background: linear-gradient(135deg, #11998e 0%, #38ef7d 100%);
  transform: scale(1.05);
}

#postsContainer {
  box-sizing: border-box;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
}

.post {
  background-color: #fff8dc;
  border-left: 5px solid orange;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  display: flex;
  box-shadow: 0 0 10px #0000001a;
}

.user-name, .user-title {
  color: #ff4500;
  margin: 0 0 5px;
  font-size: 1.2rem;
}

.user-content {
  color: #333;
  margin-bottom: 10px;
  font-size: 1rem;
}

.edit-post-btn, .delete-post-btn {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 5px 10px;
  font-size: .9rem;
}

.edit-post-btn {
  color: #fff;
  background-color: #1e90ff;
}

.delete-post-btn {
  color: #fff;
  background-color: #dc143c;
}

.comments-box {
  background-color: #f0f8ff;
  border-radius: 8px;
  margin-top: 15px;
  padding: 10px;
}

.comments-box h3 {
  color: #20b2aa;
  margin-top: 0;
  font-size: 1.1rem;
}

.comments-box ul {
  max-height: 120px;
  margin-bottom: 10px;
  padding-left: 20px;
  list-style-type: disc;
  overflow-y: auto;
}

.comments-box li {
  margin-bottom: 5px;
  font-size: .9rem;
}

.create-comment-form {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.comment-input {
  border: 1px solid #20b2aa;
  border-radius: 5px;
  flex-grow: 1;
  min-width: 0;
  padding: 5px;
  font-size: .9rem;
}

.add-comment-btn {
  color: #fff;
  cursor: pointer;
  background-color: #32cd32;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: .9rem;
}
